*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #0b2434;
  padding: 20px;
}

main{
  background-color: #f5f5f5;
  height: 400px;
  max-width: 800px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dado{
  width: 30px;
  height: 30px;
  border: 1px solid blue;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.contenedor-dados{
  width: 400px;
  height: 200px;
  margin-bottom: 20px;
  display: grid;
  grid-template-columns: repeat(5,1fr);
  justify-items: center;
  align-items: center;
  border: 1px solid red;
}
.roll{
  width: 50px;
  height: 20px;
  border: 1px solid blue;
  cursor: pointer;
}
.roll:active{
  background-color: cornflowerblue;
}

.elegido{
  background-color: cornflowerblue;
}
.no-elegido{
  background-color: white;
}